import React, { useState } from 'react';
import axios from 'axios';
import './Letters.css';
import { FaReplyd } from 'react-icons/fa';
import { MdSubdirectoryArrowRight } from 'react-icons/md';
import { BiHighlight } from 'react-icons/bi';

const Render = (props) => {
  const [toggle, setToggle] = useState(false);
  const [replytoggle, setReplyToggle] = useState(false);
  const [updateToggle, setUpdateToggle] = useState(false);
  const [reply, setReply] = useState('');
  const {
    reRending,
    removeLetter,
    AiOutlineDelete,
    BiCommentEdit,
    letter,
    LetterUpdate,
  } = props;
  //ëŒ“ê¸€ì“°ê¸°í† ê¸€
  const revisedToggle = () => {
    setToggle(!toggle);
  };
  //ëŒ“ê¸€í† ê¸€
  const replyToggle = () => {
    setReplyToggle(!replytoggle);
  };
  //ìˆ˜ì •í† ê¸€
  const updateLetterToggle = () => {
    setUpdateToggle(!updateToggle);
  };
  const handleReplyChange = (e) => {
    setReply(e.target.value);
  };
  const handleReplySubmit = (e) => {
    axios.defaults.headers.common['Access-Control-Allow-Origin'] = '*';
    e.preventDefault();
    if (!reply || /^\s*$/.test(reply)) {
      alert('ì“¸ ë§ì´ ê·¸ë ‡ê²Œ ì—†ë‹ˆ ë„ëŒ€ì²´?');
      return;
    } else {
      axios.post(
        `https://letters-heroku.herokuapp.com/api/comments/${e.target.id}`,
        {
          name: localStorage.getItem('name'),
          cmt: reply,
          createdAt: new Date().toLocaleString(),
        }
      );
      alert('ì œì¶œë˜ì—ˆìŠµë‹ˆë‹·');
      setReply('');
      reRending();
    }
  };
  const removeReply = (e) => {
    axios.defaults.headers.common['Access-Control-Allow-Origin'] = '*';
    e.preventDefault();
    let postId = e.target.id.split(',')[0];
    let cmtId = e.target.id.split(',')[1];
    if (window.confirm('ì§„ì§œ ëŒ“ê¸€ ì§€ìš¸êº¼ì•¼?')) {
      axios.delete(
        `https://letters-heroku.herokuapp.com/api/comments/${postId}/${cmtId}`
      );
      alert('ì‚­ì œë˜ì—ˆìŠµë‹ˆë‹·');
      reRending();
    } else {
      return;
    }
  };
  return (
    <div className="letters-wrapper">
      {letter.createdAt.toLocaleString()}
      <div className="letter-author">
        {letter.name === 'ë³‘êµ­'
          ? `${letter.name}ì´ê°€ ìˆ˜ë¹ˆì´ì—ê²Œ`
          : `${letter.name}ì´ê°€ ë³‘êµ­ì´ì—ê²Œ`}
      </div>
      {
        <div className="letters-content" key={letter._id}>
          {!updateToggle ? (
            <div dangerouslySetInnerHTML={{ __html: letter.msg }} />
          ) : (
            <LetterUpdate letter={letter} />
          )}
          <div className="letters-content-wapper">
            <span className="letters-content-delete">
              <AiOutlineDelete
                id={letter._id}
                onClick={(e) => removeLetter(e)}
              />
            </span>
            <span className="letters-content-reply" onClick={replyToggle}>
              <FaReplyd id={letter._id} />({letter.cmt.length})
            </span>
            {replytoggle &&
              letter.cmt.map((t) => {
                return (
                  <div>
                    <div className="letters-reply-wapper" key={t._id}>
                      <div className="replyTime">{t.createdAt}</div>
                      <span className="replyComment">
                        <MdSubdirectoryArrowRight />
                        {t.name}
                      </span>
                      <div>{t.cmt}</div>
                      <div className="letters-reply-delete">
                        <AiOutlineDelete
                          id={[letter._id, t._id]}
                          onClick={removeReply}
                        />
                      </div>
                    </div>
                  </div>
                );
              })}
            <span className="letters-content-modify" onClick={revisedToggle}>
              <BiCommentEdit />
            </span>
            {toggle && (
              <form
                className="letters-reply-form"
                onSubmit={handleReplySubmit}
                id={letter._id}
              >
                <input
                  onChange={handleReplyChange}
                  type="Text"
                  name="name"
                  autoComplete="off"
                  value={reply}
                  placeholder="ì–´ë–¤ ëŒ“ê¸€ì„ ë‹¬êº¼ë‹ˆ?"
                  required
                />

                <button className="button">ì œì¶œ</button>
              </form>
            )}
            <span>
              <BiHighlight reRending={reRending} onClick={updateLetterToggle} />
            </span>
          </div>
          <div></div>
        </div>
      }
    </div>
  );
};
export default Render;
